#product {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.content p {
    word-break: break-word
}

.card {
    min-width: 300px;
    min-height: 400px;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: 2px 8px 20px #ddd;
    margin: 10px;
    transition: 0.5 linear;
}

.card:hover {
    box-shadow: none;
}

.card img {
    max-width: 300px;
    width: 100%;
    height: 100%;
    max-height: 500px;
    display: block;
    object-fit: cover;
}

.card h3 {
    text-transform: uppercase;
    margin: 10px 0;
}

.card h3 a {
    text-decoration: none;
    color: #333;
}

.card h3 a:hover {
    color: crimson;
}

.card p {
    margin: 10px 0;
    max-width: 300px;

}

.card span {
    color: green;
}

.card button {
    border: none;
    outline: none;
    background: #333;
    color: white;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
    border-radius: 5px;
}
